import React, { useState, useEffect } from 'react'
import axios from 'axios';
import { Form } from 'react-bootstrap';
import { baseUrl } from '../config/config'
import * as ReactBootstrap from 'react-bootstrap';

export default function NvEvent() {
    const [loading, setLoading] = useState(false);
    const [intitule, setIntutile] = useState();
    const [description, setDescrition] = useState();
    const [cat, setCat] = useState();
    const [empl, setEmpl] = useState();
    const [obs, setObs] = useState();

    const [doc, setDoc] = useState()
    const [file, setFile] = useState()
    const [filearchive, setfileArchive] = useState()

    const [erreur, setErreur] = useState('')
    const [reussie, setReussie] = useState('')

    const [listobs, setListObs] = useState([])
    const [listcat, setListCat] = useState([])
    const [listempl, setListEmpl] = useState([])

    const SubmitEvent = e => {
        setLoading(true)
        setErreur('')
        setReussie('')
        e.preventDefault()
        const data = new FormData();
        data.append('intitule', intitule);
        data.append('description', description);
        data.append('cat', cat);
        data.append('empl', empl);
        data.append('obs', obs);

        data.append('file', file);
        data.append('filearchive', filearchive);
        data.append('doc',doc)
        console.log(filearchive)
        axios.post(baseUrl + 'archive', data).then(res => {
            alert(data)
                if (res.data.erreur) { 
                    setErreur(res.data.erreur); 
                    setLoading(false); 
                }
                else {
                    if (res.data.reussie) {
                        setLoading(false);
                        setReussie(res.data.reussie);
                        document.getElementById('myform').reset();
                    }
                }
            }).catch(err => {
                setErreur(err)
                setLoading(false);
            })
    }

    useEffect(() => {
        axios.get(baseUrl + 'list/obs').then((res => {
            setListObs(res.data)
        }))
    }, [])
    useEffect(() => {
        axios.get(baseUrl + 'list/cat').then((res => {
            setListCat(res.data)
        }))
    }, [])

    useEffect(() => {
        axios.get(baseUrl + 'list/emp').then((res => {
            setListEmpl(res.data)
        }))
    }, [])

    return (
        <div>
            {
                (loading === true) ?
                    <div className='d-flex justify-content-center align-items-center' style={{ height: 500 }}>
                        <ReactBootstrap.Spinner animation="border" />
                    </div>
                    :
                    <div>
                        <div className="page-header">
                            <h3 className="page-title"> Archive </h3>
                            <nav aria-label="breadcrumb">
                                <ol className="breadcrumb">
                                    <li className="breadcrumb-item"><a href="!#">Archive</a></li>
                                    <li className="breadcrumb-item active" aria-current="page">Nouveau</li>
                                </ol>
                            </nav>
                        </div>
                        <div className="row">
                            <div className="col-12 grid-margin">
                                <div className="card">
                                    <div className="card-body">
                                        <h4 className="card-title mb-5">Ajouter un archive </h4>
                                        {
                                            (erreur && erreur !== '') && <div className="alert alert-danger text-center" role="alert">{erreur}</div>
                                        }
                                        {
                                            (reussie && reussie !== '') && <div className="alert alert-success text-center" role="alert">{reussie}</div>
                                        }
                                        <form className="form-sample" id="myform" action="" enctype="multipart/form-data" onSubmit={SubmitEvent}>
                                            <div className="row">
                                                <div className="col-md-12">
                                                    <Form.Group className="row">
                                                        <label className="col-sm-2 col-form-label">Intitule</label>
                                                        <div className="col-sm-10">
                                                            <Form.Control type="text" required placeholder='votre intitule' onChange={(e) => setIntutile(e.target.value)} />
                                                        </div>
                                                    </Form.Group>
                                                </div>
                                                <div className="col-md-12">
                                                    <Form.Group className="row">
                                                        <label className="col-sm-2 col-form-label">Description</label>
                                                        <div className="col-sm-10">
                                                            <Form.Control type="text" required placeholder='votre description' onChange={(e) => setDescrition(e.target.value)} />
                                                        </div>
                                                    </Form.Group>
                                                </div>
                                                <div className="col-md-12">
                                                    <Form.Group className="row">
                                                        <label className="col-sm-2 col-form-label">Categorie</label>
                                                        <div className="col-sm-10">
                                                            <select className="form-control" required onChange={(e) => setCat(e.target.value)}>
                                                                <option value=''> - - Selectionner une categorie - -</option>
                                                                {listcat.length > 0 && listcat.map((item, index) => <option value={item.id_cat} key={index}>{item.cat}</option>)}
                                                            </select>
                                                        </div>
                                                    </Form.Group>
                                                </div>
                                                <div className="col-md-12">
                                                    <Form.Group className="row">
                                                        <label className="col-sm-2 col-form-label">Observation</label>
                                                        <div className="col-sm-10">
                                                            <select className="form-control" required onChange={(e) => setObs(e.target.value)}>
                                                                <option value=''> - - Selectionner une observation - -</option>
                                                                {listobs.length > 0 && listobs.map((item, index) => <option value={item.id_obs} key={index}>{item.obs}</option>)}
                                                            </select>
                                                        </div>
                                                    </Form.Group>
                                                </div>
                                                <div className="col-md-12">
                                                    <Form.Group className="row">
                                                        <label className="col-sm-2 col-form-label">Emplacement</label>
                                                        <div className="col-sm-10">
                                                            <select className="form-control" required onChange={(e) => setEmpl(e.target.value)}>
                                                                <option value=''> - - Selectionner une emplacement - -</option>
                                                                {listempl.length > 0 && listempl.map((item, index) => <option value={item.id_empl} key={index}>{item.emp}</option>)}
                                                            </select>
                                                        </div>
                                                    </Form.Group>
                                                </div>

                                                <div className="col-sm-12">
                                                    <Form.Group className="row">
                                                        <label className="col-sm-2 col-form-label">Image de couverture</label>
                                                        <div className="col-sm-10">
                                                            <div className="custom-file">
                                                                <input type="file" id="" name='file' required onChange={(e) => setFile(e.target.files[0])} className="form-control" accept="image/*" />
                                                            </div>
                                                        </div>
                                                    </Form.Group>
                                                </div>
                                                <div className="col-sm-12">
                                                    <Form.Group className="row">
                                                        <label className="col-sm-2 col-form-label">Image  archiver</label>
                                                        <div className="col-sm-10">
                                                            <div className="custom-file">
                                                                <input type="file" multiple id="" name='filearchive' onChange={(e) => setfileArchive(e.target.files[0])} className="form-control" accept="image/*" />
                                                            </div>
                                                        </div>
                                                    </Form.Group>
                                                </div>
                                                <div className="col-sm-12">
                                                    <Form.Group className="row">
                                                        <label className="col-sm-2 col-form-label">Document</label>
                                                        <div className="col-sm-10">
                                                            <div className="custom-file">
                                                                <input type="file" id="" name='doc' onChange={(e) => setDoc(e.target.files[0])} className="form-control" accept=".docx,.doc,.pdf" />
                                                            </div>
                                                        </div>
                                                    </Form.Group>
                                                </div>
                                            </div>
                                            {/* <div className="row">
                                                    <div className="col-md-12">
                                                        <Form.Group className="row">
                                                            <label className="col-sm-2 col-form-label">Date of Birth</label>
                                                            <div className="col-sm-10">
                                                                <DatePicker className="form-control w-100"
                                                                    selected={this.state.startDate}
                                                                    onChange={this.handleChange}
                                                                />
                                                            </div>
                                                        </Form.Group>
                                                    </div>
                                                </div> */}
                                            <button type="submit" className="btn btn-info mr-2">Enregistrer</button>
                                            <button type="reset" className="btn btn-danger">Annuler</button>
                                        </form>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
            }
        </div>
    )
}
